body {
  overflow: hidden;
}
.blocks {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
}

.block {
  --sz: 8vmin;
  --tX: 0;
  --animation: 700ms cubic-bezier(0.3, 0.5, 0.4, 0.9) infinite alternate-reverse;
  --hm: 4.5vmin;
  height: var(--sz);
  width: var(--sz);
  background-image: var(--bg);
  border-radius: 50%;
  transform: translateX(var(--tX));
  mix-blend-mode: lighten;
}

.orange {
  --bg: linear-gradient(-50deg, #fbab7e 0%, #f7ce68 100%);
  margin-right: var(--hm);
  animation: attract-orange var(--animation);
}

.blue {
  --bg: linear-gradient(50deg, #00bfd5 0%, #c5f5ff 100%);
  margin-left: var(--hm);
  animation: attract-blue var(--animation);
}

@keyframes attract-orange {
  to {
    transform: translateX(calc(var(--sz) + calc(var(--hm) / 4)));
  }
}

@keyframes attract-blue {
  to {
    transform: translateX(calc(var(--sz) * -1 - calc(var(--hm) / 4)));
  }
}
.img-matching {
  width: 100%;
}
.result-panel {
  display: flex;
  flex-direction: column;
  padding: 0 16px;
}
.count-down {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-image: linear-gradient(to right, #006175 0%, #00a950 100%);
  position: relative;
  margin: 0 auto;
  margin-top: 30%;
}
.count-down-number {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #00a84f;
  font-weight: 500;
  font-size: 40px;
  background: #e7e8e9;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.score-container {
  width: 200px;
  height: 80px;
  position: absolute;
  bottom: 16px;
  right: 16px;
  background: linear-gradient(-45deg, #2ae88a 0%, #08aeea 100%);
  border-radius: 100px;
}
#score-placeholder {
  background: white;
  position: absolute;
  width: 95%;
  height: 90%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 26px;
}
